/* Chat 전체 컨테이너 */
.chat-container {
  max-width: 600px;
  margin: 40px auto;
  background-color: #ffffff;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  height: 80vh;
}

/* 방 제목 */
.chat-container h2 {
  margin: 0 0 10px 0;
  font-size: 1.5rem;
  text-align: center;
  color: #333;
}

/* 로그아웃 버튼 */
.logout-btn {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 6px;
  cursor: pointer;
  align-self: flex-end;
  margin-bottom: 10px;
  transition: background 0.2s ease;
}

.logout-btn:hover {
  background-color: #c0392b;
}

/* 채팅 메시지 박스 */
.chat-box {
  flex: 1;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
  overflow-y: auto;
  background-color: #f9f9f9;
  margin-bottom: 10px;
}

/* 개별 메시지 */
.chat-box div {
  margin-bottom: 8px;
  padding: 6px 10px;
  border-radius: 8px;
  max-width: 80%;
  word-wrap: break-word;
}

/* 본인이 보낸 메시지 */
.chat-box .own {
  background-color: #007bff;
  color: white;
  margin-left: auto;
}

/* 다른 사람이 보낸 메시지 */
.chat-box div:not(.own) {
  background-color: #ececec;
  color: #333;
}

/* 메시지 입력 영역 */
.input-area {
  display: flex;
  gap: 10px;
}

/* 메시지 입력창 */
.input-area input {
  flex: 1;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

/* 전송 버튼 */
.input-area button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.input-area button:hover {
  background-color: #0056b3;
}
